.content__banner {
    position: relative;
}

.banner__blur {
    position: absolute;
    width: 100%;
    height: 99.5%;
    background: #81603f41;
}

.banner {
    width: 100%;
}

.banner__title {
    position: absolute;
    width: 100%;
    height: 99.5%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}