body:has(.filter .burger input:checked) {
    overflow: hidden;
}

.grid3X33 {
    display: grid;
    grid-template-columns: 33% 34% 33%;
}

.grid25X75 {
    display: grid;
    grid-template-columns: 25% 75%;
}

.product__filter-title {
    color: #81603F;
}

.product li {   
    margin: 24px 0;
    color: #2d2d2d79;
    font-size: var(--font20);
}

.checkbox__product {
    width: 16px;
    height: 16px;
    margin-right: 12px;
    border: 1px solid #2D2D2D39;
}

.center__product {
    align-items: center;
}

.product__filter-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 320px;
}

.product__filter-nav.top .arrow {
    width: 16px;
    height: 16px;
    background-image: url("../../img/arrow__bottom.svg");
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
}

.product__filter-nav.top .arrow.close {
    width: 16px;
    height: 16px;
    background-image: url("../../img/arrow__bottom.svg");
    background-repeat: no-repeat;
    background-position: center;
    transform: rotate(180deg);
}

.filter__setting.close {
    display: none !important;
}

.open .filter__setting {
    display: block;
}

.arrow {
    text-align: center;
}

.filter .nav__block {
    display: block;
}

.filter__btn {
    display: none;
}

@media (max-width: 1300px) {
    .filter .nav__block {
        display: flex;
        justify-content: space-around;
    }

    .product__filter-nav {
        width: 200px;
    }

   .grid25X75 {
        grid-template-columns: 100%;
    } 
}

@media (max-width: 1060px) {
    .grid3X33 {
        grid-template-columns: 50% 50%;
    }
}

