@media (max-width: 460px) {
    .filter {
        width: 100%;
        order: 0;
        justify-content: flex-start;
    }

    .filter {
        width: auto;
        align-items: flex-start;
    }

    .filter__btn {
        display: flex;
    }

    .filter__btn span {
        color: #81603F;
    }

    /* .wrapper__burger {
        max-width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    
    .wrapper__burger > span {
        display: block;
        margin-right: 16px;
    } */

    .filter .burger {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        position: relative;
        width: 100%;
        height: 32px;
        top: 0;
        right: 8px;
        margin-bottom: 12px;
        /* z-index: 2; */
        -webkit-user-select: none;
        user-select: none;
        z-index: 8;
    }

    .filter .burger input {
        display: flex;
        width: 6%;
        height: 100%;
        position: absolute;
        top: -12px;
        right: 0;
        cursor: pointer;
        opacity: 0;
    }

    .filter .burger span {
        /* display: flex; */
        width: 24px;
        height: 2px;
        margin-bottom: 5px;
        /* position: relative; */
        /* background: #81603F; */
        border-radius: 3px;
        z-index: 1;
        transform-origin: 5px 0px;
        transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    opacity 0.55s ease;
    }

    .filter .burger span:last-child {
        width: 16px;
        margin-left: 8px;
    }

    .filter .burger span:first-child {
        transform-origin: 0% 0%;
    }

    .filter .burger span:nth-last-child(1) {
        transform-origin: 0% 100%;
    }

    .filter .burger input:checked ~ span{
        opacity: 1;
        transform: rotate(45deg) translate(-2px, -10px);
        background: #81603F;
    }

    .filter .burger input:checked ~ span:nth-last-child(1){
        transform: rotate(-45deg) translate(6px, 2px);
        width: 24px;
    }

    .filter .burger:has(input:not(:checked)) ~ .nav__block {
        display: none;
    }
    
    .filter .burger:has(input:checked) ~ .nav__block {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        z-index: 7;
    }

    .filter .burger:has(input:checked) ~ .nav__block .ul__nav {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0;
    }

    .filter .burger ~ .nav__block {
        position: absolute;
        width: 300px;
        max-width: 100%;
        box-shadow: 0 0 10px #85888C;
        margin: -50px 0 0 -50px;
        padding: 0 40px 40px 20px;
        padding-top: 60px;
        background-color: #FEFDFB;
        -webkit-font-smoothing: antialiased;
        transform-origin: 0% 0%;
        /* transform: translate(18%, 0); */
        transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
    }

    .filter .nav__block li {
        padding: 0;
        padding-bottom: 8px;
        transition-delay: 2s;
    }

    .product li {
        margin: 0;
    }

    /* .burger:has(input:checked) ~ .ul__nav {
        
        transform: none;
    } */
}

@media (max-width: 430px) {
    .filter .burger ~ .nav__block {
        transform: translate(33%, 0);
    }
}

@media (max-width: 414px) {
    .filter .burger ~ .nav__block {
        transform: translate(28%, 0);
    }
}

@media (max-width: 390px) {
    .filter .burger ~ .nav__block {
        transform: translate(22%, 0);
    }
}

@media (max-width: 375px) {
    .filter .burger ~ .nav__block {
        transform: translate(18%, 0);
    }
}