.card__info-two {
    padding: 0 52px;
    border-radius: 4px;
}

.card__info-two h2 {
    font-size: var(--font24);
    color: #2D2D2D;
}

.card__info-two .color__title {
    color: #81603F;
}

.card__info-two p {
    color: #2d2d2d79;
    font-size: var(--font20);
    line-height: 2;
}

@media (max-width: 1060px) {
    .card__info-two p {
      font-size: var(--font16_media);
    }
  
    .card__info-two h2 {
      font-size: var(--font20_media);
    }
}