.card__shadow:hover {
    border-radius: 4px;
    box-shadow: 0px 4px 8px rgba(30, 10, 60, 0.06), 0px 8px 12px rgba(30, 10, 60, 0.08);
    cursor: pointer;
}

.card__shadow {
    margin-right: 12px;
    margin-bottom: 12px;
}

.card {
    max-width: 500px;
    min-height: 200px;
    margin-bottom: 32px;
    padding: 12px;
}

.card__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100px;
}

.card p {
    color: #2d2d2d79;
    font-size: var(--font20);
    line-height: 2;
    text-align: center;
}

.card__img-product {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.card__img-product img {
    max-width: 300px;
}

.hint {
    display: none;
    max-width: 820px;
    position: fixed;
    border-radius: 4px;
    border: 2px solid rgba(0, 0, 0, 0.2);
    background: white;
    box-shadow: 0 0 0 1px hsla(0, 0%, 100%, .3) inset, 0 .5em 1em rgba(0, 0, 0, 0.6);
    text-shadow: 0 1px 1px hsla(0, 0%, 100%, .3);
    z-index: 9999999;
}

.slide > .card__img-product:not(.activ__img) {
    display: none;
}

.activ__img {
    display: flex;
}

.hint__show {
    display: block !important;
    top: calc((100vh - 420px) / 2);
    left: calc((100vw - 820px) / 2);
    max-height: 100%;
}

.closed {
    display: flex;
    justify-content: flex-end;
    padding: 4px;
    cursor: pointer;
}

.closed-btn {
    width: 20px;
    border-radius: 50%;
}

.closed h3 {
    margin: 0;
    text-align: center;
    color: #81603F;
}

.info__description {
    margin: 0;
    margin-bottom: 12px;
    position: relative;
    max-width: 100%;
    overflow: auto;
    /*display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;*/
    max-height: 420px;
    line-height: 16px;
    padding: 0 8px;
}

.info__description :where(h2, p) {
    line-height: 1.5;
}

.info__description p {
    color: #2d2d2d85;
}

.radius__external {
    display: flex;
    align-items: center;
    justify-content: center;

    border: 1px solid;
    border-radius: 50px;
    height: 20px;
    width: 20px;
    cursor: pointer;
}

.radius__external:not(:last-child) {
    margin-right: 12px;
}

.radius__internal {
    height: 16px;
    width: 16px;
    background: #fff;
    border-radius: 50px;
}

.activ__btn > .radius__internal {
    height: 16px;
    width: 16px;
    background: #81603F;
    border-radius: 50px;
}

.popup__bg-show {
    display: block !important;
}

.popup__bg {
    display: none;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    background: hsla(0, 0%, 100%, .7);
    z-index: 100;
}

.frame__shadow > img {
    box-shadow: 0 0 0 1px hsla(0, 0%, 100%, .3) inset, 0 .2px 1px rgba(0, 0, 0, 0.6);
    margin-bottom: 12px;
}

@media (max-width: 1060px) {
    .card p {
      font-size: var(--font16_media);
    }
}

@media(max-width: 826px) {
    .hint__show {
        top: 0;
        left: 0;
        height: 100vh;
    }

    .mob__card-product {
        flex-direction: column;
    }

    .frame__shadow > img {
        max-width: 300px !important;
    }
}

@media (max-width: 700px) {
    .hint__show {
        position: fixed;
        left: 0;
    }

    .popup__bg {
        position: fixed;
    }
}

@media (max-width: 680px) {
    .card__img-product img {
        max-width: 150px;
    }
}