body:has(.menu .burger input:checked) {
    overflow: hidden;
}

.wrapper__phone, 
.wrapper__leng,
.wrapper__logo,
.wrapper__navigation {
    display: flex;
    align-items: center;
}

header .wrapper__block {
    width: 100%;
    justify-content: flex-end;
}

.wrapper__navigation {
    width: 100%;
}

.menu {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.nemu__toggle-mob {
    width: 100%;
}

header ul {
    list-style: none;
    display: flex;
    position: relative;
    white-space: nowrap;
}

header ul li {
    display: block;
    position: relative;
}

header ul li a {
    display: inline-flex;
    padding: 12px 8px;
    color: #2d2d2d79;
    text-decoration: none;
    font-size: var(--font20);
}

header ul li a:hover {
    color: #1C1E5E;
}

header ul li a.active {
    color: #1C1E5E;
}

.wrapper__phone span {
 color: #81603F;
 padding: 12px 8px;   
}

.wrapper__leng .btn__leng {
    border: none;
    background: transparent;
    cursor: pointer;
    color: #2d2d2d79;
}

.btn__leng.active {
    color: #1C1E5E;
}

.btn__leng:hover {
    color: #1C1E5E;
}

.burger {
    display: none;
}

.nav__block {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

@media (max-width: 1060px) {
    .card p {
      font-size: var(--font16_media);
    }
}

@media (max-width: 800px) {
    .wrapper__phone {
        display: none;
    }
}


@media (max-width: 460px) {
    header .wrapper__block {
        width: 100%;
        order: 0;
        justify-content: flex-start;
    }

    header .wrapper__logo {
        width: 100%;
    }

    .wrapper__navigation {
        width: auto;
        align-items: flex-start;
    }

    .menu .burger  {
        display: flex;
        flex-direction: column;
        position: relative;
        width: 32px;
        height: 32px;
        top: 32px;
        /* right: 8px; */
        z-index: 10;
        -webkit-user-select: none;
        user-select: none;
    }

    .menu .burger input {
        display: flex;
        width: 100%;
        height: 100%;
        position: absolute;
        top: -12px;
        right: 0;
        cursor: pointer;
        opacity: 0;
        z-index: 2;
    }

    .menu .burger span {
        display: flex;
        width: 24px;
        height: 2px;
        margin-bottom: 5px;
        position: relative;
        background: #81603F;
        border-radius: 3px;
        z-index: 1;
        transform-origin: 5px 0px;
        transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                    opacity 0.55s ease;
    }

    .menu .burger span:last-child {
        width: 16px;
        margin-left: 8px;
    }

    .menu .burger span:first-child {
        transform-origin: 0% 0%;
    }

    .menu .burger span:nth-last-child(1) {
        transform-origin: 0% 100%;
    }

    .menu .burger input:checked ~ span {
        opacity: 1;
        transform: rotate(45deg) translate(-2px, -10px);
        background: #81603F;
    }

    .menu .burger input:checked ~ span:nth-last-child(1){
        transform: rotate(-45deg) translate(2px, -2px);
        width: 24px;
    }

    .menu :where(.nav__block) {
        display: none;
    }
    
    .menu .burger:has(input:checked) ~ .nav__block {
        display: flex;
        flex-direction: column-reverse;
        justify-content: flex-end;
    }

    .menu .burger:has(input:checked) ~ .nav__block .ul__nav {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0;
    }

    .menu .burger ~ .nav__block {
        position: absolute;
        width: 180px;
        height: 400px;
        box-shadow: 0 0 10px #85888C;
        margin: -50px 0 0 -50px;
        padding: 0 40px 40px 20px;
        padding-top: 46px;
        background-color: #FEFDFB;
        -webkit-font-smoothing: antialiased;
        transform-origin: 0% 0%;
        transform: translate(-66%, 0);
        transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
        z-index: 9;
    }

    .nav__block li {
        padding: 10px 0;
        transition-delay: 2s;
    }

    /* .burger:has(input:checked) ~ .ul__nav {
        
        transform: none;
    } */
}