/* .content {
} */

.content__info-aboutUs {
    margin: 0;
}

.content__info-aboutUs {
    position: relative;
}

.block__info {
    display: grid;
    align-items: center;
    grid-template-columns: 48% 48%;
    gap: 4%;
}

.block__left,
.block__right {
    max-width: 520px;
}

.block__left {
    margin: 0 0 0 auto;
}

.block__riadok {
    position: absolute;
    display: flex;
    justify-content: center;
    height: 100%;
    width: 100%;
}

.riadok {
    border: 1px solid #81603F;
}

@media (max-width: 1380px) {
    .contract {
        gap: 0;
        grid-template-columns: 100%;
        justify-items: center;
    }

    .contract img {
        width: 100%;
        order: 1;
    }
}

@media (max-width: 1080px) {

    .white__aboutUs {
        padding: 20px;
        background: #FFFFFF;
    } 
    
    .brown__aboutUs {
        padding: 20px;
        background: #81603F;
    }
    
    .brown__aboutUs .block__info__aboutUs.info {
        color: #FFFFFF;
    }
    
    .block__riadok {
        display: none;
    }

    .block__info {
        gap: 0;
        grid-template-columns: 100%;
        width: 100%;
    }

    .img__about {
        display: none;
    }

    .block__left {
        margin: 0;
    }
}