.card__info {
    background: #81603F;
    padding: 52px;
    border-radius: 4px;
}

.card__info h2 {
    font-size: var(--font24);
    color: #FFFFFF;
}

.card__info p {
    color: #ffffff88;
    font-size: var(--font20);
    line-height: 2;
}

@media (max-width: 1080px) {

    .card__info p {
      font-size: var(--font16_media);
    }
  
    .card__info h2 {
      font-size: var(--font20_media);
    }
}