:root {
  --font24: calc(20px + 10 * (100vw / 1600));
  --font20: calc(12px + 6 * (100vw / 1600));
  --font60: calc(40px + 20 * (100vw / 1600));

  --font20_media: calc(20px + (10 + 10 * 0.7) * ((100vw - 320px) / 1600));
  --font16_media: calc(12px + (6 + 6 * 0.7) * ((100vw - 320px) / 1600));
  --font30_media: calc(18px + (15 + 15 * 0.7) * ((100vw - 320px) / 1600));
}

@font-face {
	font-family: Roboto;
	font-display: swap;
	src: local('Roboto'), url('./fonts/Roboto/Roboto-Black.ttf') format('opentype');
	font-weight: 900;
	font-style: normal;
}
@font-face {
	font-family: Roboto;
	font-display: swap;
	src: local('Roboto'), url('./fonts/Roboto/Roboto-Bold.ttf') format('opentype');
	font-weight: 400;
	font-style: normal;
}
@font-face {
	font-family: Roboto;
	font-display: swap;
	src: local('Roboto'), url('./fonts/Roboto/Roboto-Medium.ttf') format('opentype');
	font-weight: 500;
	font-style: normal;
}
@font-face {
	font-family: Roboto;
	font-display: swap;
	src: local('Roboto'), url('./fonts/Roboto/Roboto-Regular.ttf') format('opentype');
	font-weight: 400;
	font-style: normal;
}


body,
html,
#root {
  padding: 0;
  margin: 0;
  background: #FEFDFB;
  font-family: Roboto;
  height: 100%;
}

a {
  text-decoration: none;
}

.content {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 1 auto;
  position: relative;
}

.marginAuto {
  margin: auto;
}

/* footer {
  flex: 0 0 auto;
} */

.wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 1600px;
  margin: 0 auto;
}

.block {
  padding-top: 100px;
}

.title {
  color: #FEFDFB;
  font-size: var(--font60);
}

.wrapper__block {
  display: flex;
  padding: 20px 0;
}

.wrapper__block > div:first-child {
  margin-right: 20px;
  margin-left: 8px;
}

.info {
  color: #2d2d2d79;
  font-size: var(--font20);
  line-height: 2;
}

.flex__column {
  display: flex;
  flex-direction: column;
}

.flex__row {
  display: flex;
}

.flex__center {
  justify-content: center;
}

.al__center {
  align-items: center
}

.pBottom100 {
  padding-bottom: 100px;
}

.p40 {
  padding: 40px;
}

.m1rem {
  margin: 1rem;
}

.grid__column2 {
  display: grid;
  grid-template-columns: 50% 50%;
  justify-items: center;
}

.bg__fon:has(~header .burger input:checked),
.bg__fon:has(~.main .burger input:checked) {
  position: absolute;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  z-index: 6;
}

@media (max-width: 1080px) {
  .info {
    text-align: center;
    font-size: var(--font16_media);
    margin: 0 8px;
  }

  .title {
    font-size: var(--font30_media);
  }

  .block {
    padding-top: 50px;
  }

  .pBottom100 {
    padding-bottom: 50px;
  }
}

@media (max-width: 800px) {
  .wrapper__block > div:first-child {
      margin-right: 0;
  }
}

@media(max-width: 600px) {
  .grid__column2 {
    grid-template-columns: 100%;
  }
}