footer {
    border-top: 2px solid rgba(0, 0, 0, 0.15);
}

.block__footer {
    display: flex;
    padding-bottom: 100px;
}

.block__footer .column h2 {
    color: #81603F;
}

.footer__contact {
    display: flex;
    align-items: center;
}

.footer__contact span {
    padding: 0 8px;
}

.gap {
    gap: 250px;
}

@media (max-width: 1020px) {
    .block__footer {
        flex-direction: column;
    }
}

@media (max-width: 800px) {
    .nav {
        display: none;
    }

    .column {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .column .info {
        text-align: center;
    }

    footer .wrapper__block {
        justify-content: center;
    }
}