.content__ConnectUs {
    position: relative;
    z-index: 3;
}
.info__ConnectUs {
    position: fixed;
    top: 20%;
    right: 0;
    width: auto;
    height: auto;
    box-shadow: 0px 4px 8px rgba(30, 10, 60, 0.06), 0px 8px 12px rgba(30, 10, 60, 0.08);
    display: flex;

    border-radius: 4px;
}

.info__ConnectUs > p {
    
    background: #1C1E5E;
    writing-mode: vertical-rl;
    white-space: nowrap;
    padding: 32px 8px;
    cursor: pointer;
    color: white;
    border-radius: 4px;
    text-align: center;
    transform: rotate(180deg);
}

p {
    margin: 0;
}

.block__ConnectUs {
    height: 100%;
    max-width: 200px;
    padding: 0 16px;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
    border-radius: 4px 0 0 4px; 
    background-color: white;
} 

.block__ConnectUs > p {
    margin: 8px 0;
}

.hidden__ConnectUs {
    display: none;
}

.open__ConnectUs {
    display: block;
}