.block__partner-info {
    position: relative;
    height: 100%;
}

.partner-info {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.partner-info .banner {
    max-height: auto;
}

.partner-wrapper {
    position: absolute;
    width: 100%;
}

.partner-block {
    width: 100%;
    min-height: 100%;
    border-radius: 4px;
    box-shadow: 0px 4px 8px rgba(30, 10, 60, 0.06), 0px 8px 12px rgba(30, 10, 60, 0.08);
}

.white {
    background: #FFFFFF;
} 

.brown {
    background: #81603F;
}

.partner-block.brown h2 {
    font-size: var(--font24);
    color: #FFFFFF;
}

.partner-block.brown h4, 
.partner-block.brown p {
    color: #ffffff88;
    font-size: var(--font20);
    line-height: 2;
}

.partner-block.white h2 {
    font-size: var(--font24);
    color: #81603F;
}

.partner-block.white h4, 
.partner-block.white p {
    color: #2d2d2d79;
    font-size: var(--font20);
    line-height: 2;
}

@media (max-width: 1250px) {
    .swap {
        overflow-y: hidden;
        overflow-x: auto;
        max-width: 100vw;
    }
}

@media (max-width: 1060px) {
    .partner-block.brown h2 {
        font-size: var(--font20_media);
    }

    .partner-block.brown h4, 
    .partner-block.brown p {
        font-size: var(--font16_media);
    }

    .partner-block.white h2 {
        font-size: var(--font20_media);
    }

    .partner-block.white h4, 
    .partner-block.white p {
        font-size: var(--font16_media);
    }
}